// SpellingExercise/index.js - VERSION PROPRE

import { useMemo, useEffect, useCallback } from "react";
import { View, ActivityIndicator, Text } from "react-native";
import { useNavigation } from "@react-navigation/native";
import PropTypes from 'prop-types';

import Container from "../../../components/layout/Container";
import SpellingHeader from "./SpellingHeader";
import SpellingProgress from "./SpellingProgress";
import SpellingCard from "./SpellingCard";
import SpellingActions from "./SpellingActions";

import useSpelling from "./hooks/useSpelling";
import useLastActivity from "../../../hooks/useLastActivity";
import { getSpellingData, getLevelColor } from "../../../utils/spelling/spellingDataHelper";
import createStyles from "./style";

const SpellingExercise = ({ route }) => {
  const { level = "1", exerciseType = "correction" } = route.params || {};
  const navigation = useNavigation();
  const styles = createStyles();
  const { saveActivity } = useLastActivity();

  const levelColor = getLevelColor(level);

  // âœ… Extraction de la logique conditionnelle pour amÃ©liorer la lisibilitÃ©
  const getExerciseTypeName = () => {
    if (exerciseType === "correction") return "Correction";
    if (exerciseType === "rules") return "RÃ¨gles";
    return "Homophones";
  };
  
  // DÃ©finir exerciseTypeName pour tout le composant
  const exerciseTypeName = getExerciseTypeName();
  
  const spellingData = useMemo(() => {
    try {
      return getSpellingData(level, exerciseType);
    } catch (error) {
      // âœ… Gestion d'erreur appropriÃ©e
      console.warn(`Error getting spelling data for level ${level}, type ${exerciseType}:`, error);
      return null;
    }
  }, [level, exerciseType]);

  const {
    currentExerciseIndex,
    userInput,
    showHint,
    showFeedback,
    isCorrect,
    loaded,
    currentExercise,
    totalExercises,
    setUserInput,
    toggleHint,
    checkAnswer,
    handleNext,
    retryExercise,
    isLastExercise,
    hasValidData,
  } = useSpelling(spellingData, level, exerciseType);

  // âœ… AJOUTÃ‰ : Debug pour voir ce que le composant reÃ§oit du hook
  console.log('ðŸ” DEBUG SpellingExercise Component:', {
    loaded,
    hasValidData,
    currentExercise: !!currentExercise,
    currentExerciseIndex,
    totalExercises,
    spellingData: !!spellingData,
    level,
    exerciseType
  });

  useEffect(() => {
    if (loaded && hasValidData && currentExercise) {
      const saveActivityAsync = async () => {
        try {
          await saveActivity({
            title: `Orthographe ${exerciseTypeName}`,
            level,
            type: "spelling",
            metadata: {
              word: currentExerciseIndex,           // âœ… Index actuel (pour progression)
              totalWords: totalExercises,           // âœ… Total (pour progression)
              exerciseType,
              content: currentExercise.wordToCorrect || `Exercice ${currentExerciseIndex + 1}` // âœ… Contenu de l'exercice
            }
          });
        } catch (error) {
          console.error("Error saving activity:", error);
        }
      };

      saveActivityAsync();
    }
  }, [loaded, hasValidData, currentExercise, level, exerciseType, currentExerciseIndex, totalExercises, saveActivity]);

  const handleBack = useCallback(() => navigation.goBack(), [navigation]);

  if (!loaded) {
    return (
      <Container>
        <SpellingHeader
          title={`Orthographe ${exerciseTypeName}`}
          level={level}
          levelColor={levelColor}
          onBack={handleBack}
        />
        <View style={styles.loadingContainer}>
          <ActivityIndicator size="large" color={levelColor} testID="activity-indicator" />
          {/* âœ… MODERNISÃ‰ : SupprimÃ© le texte de chargement pour Ãªtre cohÃ©rent avec les autres modules */}
        </View>
      </Container>
    );
  }

  if (!hasValidData) {
    return (
      <Container>
        <SpellingHeader
          title={`Orthographe ${exerciseTypeName}`}
          level={level}
          levelColor={levelColor}
          onBack={handleBack}
        />
        {/* âœ… MODERNISÃ‰ : Utilise un message simple au lieu de styles d'erreur personnalisÃ©s */}
        <View style={styles.loadingContainer}>
          <Text style={{ fontSize: 16, color: '#64748b', textAlign: 'center' }}>
            Aucune donnÃ©e trouvÃ©e pour ce niveau.
          </Text>
        </View>
      </Container>
    );
  }

  return (
    <Container>
      <SpellingHeader
        title={`Orthographe ${exerciseTypeName}`}
        level={level}
        levelColor={levelColor}
        onBack={handleBack}
      />
      <SpellingProgress
        currentExerciseIndex={currentExerciseIndex + 1}
        totalExercises={totalExercises}
        levelColor={levelColor}
      />
      <SpellingCard
        exercise={currentExercise}
        userInput={userInput}
        showHint={showHint}
        showFeedback={showFeedback}
        isCorrect={isCorrect}
        onChangeText={setUserInput}
        onToggleHint={toggleHint}
        onCheckAnswer={checkAnswer}
        onNext={handleNext}
        onRetry={retryExercise}
        isLastExercise={isLastExercise}
        levelColor={levelColor}
      />
      <SpellingActions
        showFeedback={showFeedback}
        isCorrect={isCorrect}
        userInput={userInput}
        isLastExercise={isLastExercise}
        exerciseType={exerciseType}
        levelColor={levelColor}
        onCheck={checkAnswer}
        onNext={handleNext}
        onRetry={retryExercise}
      />
    </Container>
  );
};

export default SpellingExercise;

// âœ… Ajout de la validation des props
SpellingExercise.propTypes = {
  route: PropTypes.shape({
    params: PropTypes.shape({
      level: PropTypes.string,
      exerciseType: PropTypes.string,
    }),
  }).isRequired,
};